.table-basic {
    &__rating-result-item {
        display: flex;
        align-items: center;
        padding-left: 24px;
        padding-right: 24px;
        padding-top: 16px;
        padding-bottom: 16px;
        color: #402E25;

        &:nth-child(even) {
            background-color: rgba(141, 102, 74, .3); } }

    &__rating-result-number {
        width: 50px; }

    &__rating-result-points {
        margin-left: auto;
        font-family: $bold; }

    &__row {
        display: flex;
        align-items: center;
        color: #402E25;
        padding-left: 24px;
        padding-right: 24px;
        padding-top: 16px;
        padding-bottom: 16px;

        &:nth-child(even) {
            background-color: rgba(141, 102, 74, .3); } }

    &__item {}

    &__item-versus {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-column-gap: 38px;
        position: relative;
        min-width: 54%;

        a {
            text-decoration: none;
            color: #402E25;
            font-family: $bold; } }

    &__item-id {
        width: 84px; }

    &__item-button {
        margin-left: auto;
        max-width: 110px; }

    &__item-place {
        width: 28px;
        font-family: $medium; }

    &__item-score {
        margin-left: auto;
        display: grid;
        grid-column-gap: 6px;
        grid-template-columns: 24px 24px;
        text-align: center;
        font-family: $bold; }

    &__item-count {
        font-family: $bold;
        margin-left: auto;
        margin-right: auto; }

    &__versus-person {
        flex-grow: 1;

        &--right {
            text-align: right; } }

    &__versus-text {
        font-size: 16px;
        color: #402E25;
        font-family: $medium;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%); }

    &--transparent {

        .table-basic__row {
            background-color: transparent; } }

    &__tournaments-date {
        width: 13.7%; }

    &__tournaments-prize {
        display: flex;
        width: 28.8%; }

    &__tournaments-price {
        width: 17.6%; }

    &__tournaments-item-title {
        width: 27.3%; }

    &__tournaments-prize-item {
        display: flex;
        align-items: center;
        color: #402E25;
        margin-left: 4px;
        margin-right: 4px; }

    &__tournaments-prize-status {
        width: 19px;
        height: 19px;
        border-radius: 50%;
        color:  #ffffff;
        font-size: 12px;
        line-height: 10px;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-right: 6px;

        &--first {
            background-color: #ECB21D; }

        &--second {
            background-color: #A8A8A8; }

        &--third {
            background-color: #C34F1D; } }

    &__tournaments-button {
        font-size: 14px;
        line-height: 24px;
        padding-left: 14px;
        padding-right: 14px;
        padding-top: 9px;
        padding-bottom: 7px;
        color: #ffffff;
        border-radius: 4px;
        margin-left: auto;
        border: none;
        font-family: $bold;
        cursor: pointer;
        width: 94px;
        text-align: center;
        display: flex;
        justify-content: center;

        &--blue {
            background-color: #60A3B9; }

        &--green {
            background-color: #83AB30; }

        &--red {
            background-color: #C5252D; } } }
